import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import { Button, Carousel } from "react-bootstrap";
import { fetchAction } from "../redux/addvertising/adds.actions";
import "./adsScreen.css";
const AdsScreen = () => {
  const dispatch = useDispatch();
  const state = useSelector((state) => state);
  const handleFetch = () => {
    dispatch(fetchAction());
  };
  return (
    <>
      <div className="nav-container">
        <Button
          variant="success"
          onClick={handleFetch}
          className="fetch-data-btn"
        >
          Fetch Data
        </Button>
              <Link to="/adds-screen-task/dashbord" style={{ textDecoration: "none", color: "black" }}>
          <Button variant="primary"> Dash Bord</Button>
        </Link>
      </div>
      <Carousel>
        {state.adds.map((add) =>
          add.image ? (
            <Carousel.Item interval={add.durarion}>
              <img
                className="d-block w-100 adds-screen-imgs"
                src={add.image}
                alt=""
                key={add.id}
                id={add.id}
              />
            </Carousel.Item>
          ) : (
            <Carousel.Item interval={(add.duration + 1) * 1000}>
              <video
                id={add.id}
                width="100%"
                height="176"
                controls
                className="d-block w-100 h-100"
                key={add.id}
              >
                <source src={add.video} type="video/mp4" />
              </video>
            </Carousel.Item>
          )
        )}

      </Carousel>

    </>
  );
};

export default AdsScreen;
