import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import { Button, Table } from "react-bootstrap";
import "./dashbord.css";
import { deleting } from "../redux/addvertising/adds.actions";
import NewAdd from "./new-adds.js";
import EditUI from "./edit-ui.js";
const DashBord = () => {
  const dispatch = useDispatch();
  const state = useSelector((state) => state);
  const [modalShow, setModalShow] = useState(false);
  const [editShow, setEditShow] = useState(false);
  const [add, setAdd] = useState({});
  console.log(state);
  const handleBacktoHome = () => {
    return;
  };
  const handleDelBtn = (id) => {
    dispatch(deleting(id));
    return;
  };
  const handleAddNewAdds = () => {
    setModalShow(true);
    return;
  };
  const handleEditBtn = (add) => {
    setEditShow(true);
    setAdd(add);
    return;
  };
  return (
    <>
      <NewAdd show={modalShow} onHide={() => setModalShow(false)} />
      <EditUI show={editShow} onHide={() => setEditShow(false)} add={add} />
      <Link
              to="/adds-screen-task"
        style={{ textDecoration: "none", color: "black" }}
        onClick={handleBacktoHome}
      >
        <Button variant="primary"> Home</Button>
      </Link>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th>order</th>
            <th>duration</th>
          
            <th>Video / image</th>
            <th>Edit</th>
            <th>Delete</th>
          </tr>
        </thead>
        <tbody>
          {state.adds.map((add) => (
            <>
              <tr key={add.id} id={add.id}>
                <td>{add.order}</td>
                <td>{Math.round(add.duration)} s</td>
                
                <td className="image-video-container">
                  {add.type === "video" ? (
                    <video id="myVideo" className="dashbord-video" controls>
                      <source src={add.video} type="video/mp4" />
                    </video>
                  ) : (
                    <img className="dashbord-image" src={add.image} alt="" />
                  )}
                </td>
                <td>
                 
                  <i
                    className="bi bi-gear"
                    onClick={() => handleEditBtn(add)}
                  ></i>
                </td>

                <td>
                
                  <i
                    className="bi bi-trash3-fill"
                    onClick={() => handleDelBtn(add.id)}
                  ></i>
                </td>
              </tr>
            </>
          ))}
          <tr>
            <td colSpan={5}>
              <Button
                variant="primary"
                width={{ width: "100%" }}
                onClick={handleAddNewAdds}
              >
                Add video/image
              </Button>
            </td>
          </tr>
        </tbody>
      </Table>
    </>
  );
};

export default DashBord;
