import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Modal, Button } from "react-bootstrap";
import { editing } from "../redux/addvertising/adds.actions";
import "./new-adds.css";
const EditUI = (props) => {
  const oldAddsstate = useSelector((state) => state.adds);
  const dispatch = useDispatch();
  const [editCount, seteditCount] = useState(1);
  useEffect(() => {
    seteditCount(props.add.id);
  }, [props.add.id]);
  const [type, setType] = useState("");
  let [duration, setDuration] = useState(1000);
  const handleClose = () => {
    props.onHide();
    return;
  };
  let [videoUrl, setvideoUrl] = useState("");
  let [imgUrl, setimgUrl] = useState("");
  const handleUpload = (e) => {
    const video = document.getElementById("edit-video-output");
    const image = document.getElementById("edit-image-output");

    if (e.files[0].type.includes("video")) {
      setvideoUrl(URL.createObjectURL(e.files[0]));
      setimgUrl("");
      image.src = "";
      video.src = URL.createObjectURL(e.files[0]);
      video.style.display = "inline-block";
      setType("video");
      setTimeout(() => {
        setDuration(video.duration);
      }, 2000);
    } else if (e.files[0].type.includes("image")) {
      setimgUrl(URL.createObjectURL(e.files[0]));
      setvideoUrl("");
      video.removeAttribute("src");
      document.getElementById("video-source").removeAttribute("src");
      image.src = URL.createObjectURL(e.files[0]);
      setType("image");
      setDuration(3);
    }
    return 0;
  };
  const handleUp = () => {
    if (editCount < oldAddsstate.length) {
      seteditCount(editCount + 1);
      return 0;
    }
  };
  const handleDown = () => {
    if (editCount > 1) {
      seteditCount(editCount - 1);
      return 0;
    }
  };
  const handleSubmitAdds = () => {
    if (videoUrl || imgUrl) {
      dispatch(
        editing(
          props.add.id,
          editCount,
          editCount,
          videoUrl,
          imgUrl,
          type,
          duration
        )
      );
      handleClose();
      setvideoUrl("");
      setimgUrl("");
      return;
    } else {
      window.alert("upload video or img firstly!");
      return;
    }
  };
  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Edit video / image
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <div className="file-container">
          <div className="uploaded-file-container">
            <input
              accept="image/*,video/*"
              onChange={(e) => handleUpload(e.target)}
              type="file"
              id="selectedFile"
            />
            <p style={{ marginTop: "1rem" }}>
              <img id="edit-image-output" width="200" alt="" />
              <video
                id="edit-video-output"
                controls
                style={{ width: "180px", height: "auto", display: "none" }}
              >
                <source id="video-source" type="video/mp4" />
              </video>
            </p>

            <span
              disabled
              style={{ display: "block", fontSize: 14, marginTop: 3 }}
            >
              upload video or iamge only
            </span>
          </div>
          <p>choose the order of your addvertise to be shown in</p>
          <input
            type="text"
            value={editCount}
            disabled
            className="btn btn-info show-order-inpt"
          />
          <button onClick={handleUp}>up</button>
          <button onClick={handleDown}>down</button>
        </div>
      </Modal.Body>
      <Modal.Footer>
        <Button onClick={handleSubmitAdds} variant="success">
          Save
        </Button>
        <Button onClick={handleClose} variant="danger">
          Close
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default EditUI;
