import { REQUEST, SUCCESS, FAILED, DELETE, EDIT, Add } from "./actionsTypes";

export const fetchReq = () => {
  return {
    type: REQUEST,
  };
};
export const fetchSucceed = (adds) => {
  return {
    type: SUCCESS,
    adds,
  };
};
export const fetchFaild = (error) => {
  return {
    type: FAILED,
    error,
  };
};

const contentful = require("contentful");
export const fetchAction = () => {
  return (dispatch) => {
    dispatch(fetchReq);
    const client = contentful.createClient({
      space: "0zy20tnjnzt9",
      accessToken: "7-Ke9kA7HGfAQu6dUYFzxgH9krWEVUwU2eBmphfnSqI",
    });
    client
      .getEntry({ content_type: "adds" })
      .then((response) => {
        dispatch(fetchSucceed(response.fields.data));
      })
      .catch((error) => {
        const errorMsg = error.message;
        dispatch(fetchFaild(errorMsg));
      });
  };
};

export const adding = (id, order, video, image, fileType, dur) => {
  return {
    type: Add,
    id,
    order,
    video,
    image,
    fileType,
    dur,
  };
};
export const deleting = (id) => {
  return {
    type: DELETE,
    id,
  };
};
export const editing = (oldOrder, id, order, video, image, fileType, dur) => {
  return {
    type: EDIT,
    oldOrder,
    id,
    order,
    video,
    image,
    fileType,
    dur,
  };
};
