import { REQUEST, SUCCESS, FAILED, DELETE, EDIT, Add } from "./actionsTypes";
export let initState = {
  loading: false,
  adds: [],
  error: ``,
};

const addsReducer = (state = initState, action) => {
  switch (action.type) {
    case REQUEST:
      return { ...state, loading: true };
    case SUCCESS:
      action.adds.map((item, i) => {
        item.id = i + 1;
        item.order = i + 1;
        item.type = item.video ? "video" : "image";
        item.duration = item.video ? 10 : 3;
        item.video = item.video
          ? "https://cdn.videvo.net/videvo_files/video/free/2013-07/large_watermarked/hd0079_preview.mp4"
          : "";
        return item;
      });
      state.adds.push(...action.adds);
      state.adds.map((add, i) => {
        add.id = i + 1;
        add.order = i + 1;
        return add;
      });
      return { ...state, error: ``, loading: false };
    case FAILED:
      return { adds: [], loading: false, error: action.error };
    case DELETE:
      const addsData = state.adds.filter((add) => add.id !== action.id);
      addsData.map((add, i) => {
        add.id = i + 1;
        add.order = i + 1;
        return add;
      });
      return { error: ``, loading: false, adds: addsData };
    case EDIT:
      const newEditedData = {
        id: action.id,
        order: action.order,
        video: action.video,
        image: action.image,
        type: action.fileType,
        duration: action.dur,
      };
      state.adds.splice(action.oldOrder - 1, 1);
      state.adds.splice(action.order - 1, 0, newEditedData);
      state.adds.map((add, i) => {
        add.id = i + 1;
        add.order = i + 1;
        return add;
      });
      return { ...state };
    case Add:
      const newAddedData = {
        id: action.id,
        order: action.order,
        video: action.video,
        image: action.image,
        type: action.fileType,
        duration: action.dur,
      };
      state.adds.splice(action.order - 1, 0, newAddedData);
      state.adds.map((add, i) => {
        add.id = i + 1;
        add.order = i + 1;
        return add;
      });
      return { ...state };
    default:
      return { ...state };
  }
};

export default addsReducer;
